body {
  margin: 0;
  font-family: 'Inter';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #d0ff93;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 0 auto;
}

.overlay {
  position: absolute;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: 2;

}

.drawer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
 overflow: auto;
  position: absolute;
  right: 0;
  width: 385px;
  height: 100%;
  background-color: #FFFFFF;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);

  & h2 {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
    padding: 30px;

    display: flex;
    justify-content: space-between;
  }
}

.header {
  margin-top: 40px;
  padding: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #EAEAEA;

  .headerLogo {
    display: flex;
    
  }

  .cart {
    cursor: pointer;
  }

  .headerInfo {
    padding-left: 20px;

    & h1 {
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 24px;
      text-transform: uppercase;
    }

    & p {
      font-weight: 400;
      font-size: 14px;
      line-height: 17px;
      color: #9D9D9D;
    }
  }

  .headerMenu {
    & ul {
      display: flex;
      gap: 30px;
      list-style: none;

      & span {
        padding-left: 10px;
        font-weight: 600;
        font-size: 14px;
        line-height: 17px;
        color: #5C5C5C;
      }
    }
  }
}

.cartItems {
  padding-left: 30px;
  overflow: auto;
  flex: 1;

  .cartItem {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    max-width: 325px;
    border: 1px solid #F3F3F3;
    border-radius: 20px;
    margin-bottom: 20px;

    .cartItemImg {
      width: 70px;
      height: 70px;
      background-size: contain;
      background-repeat: no-repeat;
      margin-left: 20px;
    }

    .cartItemInfo {
      font-size: 14px;
      line-height: 17px;
      padding: 30px 19px;
      max-width: 150px;

      & p {
        font-weight: 400;
        padding-bottom: 8px;

      }

      & b {
        font-weight: 700;
      }
    }
  }
}

.cartItemButton {
  cursor: pointer;
  opacity: 0.5;

  &:hover {
    opacity: 0.9;
  }

}

.cartTotal {
  padding: 30px;

  & li {
    display: flex;
    justify-content: space-between;
    padding-bottom: 20px;
  }

  div {
    flex: 1;
    border-bottom: 1px dashed #DFDFDF;
    margin: 0px 10px;
  }


}

.btn-green {
  width: 325px;
  height: 55px;
  left: 895px;
  top: 974px;
  color: #FFFFFF;
  font-weight: 600;
  font-size: 16px;
  background: #9DD558;
  border-radius: 18px;
  border: none;
  // position: relative;

  &:hover {
    background: lighten($color: #87dd1d, $amount: 0);
  }
}

.cartEmpty {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 50px;
  flex: 1;
  // max-width: 285px;
// 
  h2 {
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
  }

  p {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    opacity: 0.4;
    padding-bottom: 40px;
    text-align: center;
  }
}


.products {
  padding: 60px;

  .productsTitle {
    display: flex;
    justify-content: space-between;

    & h1 {
      font-weight: 700;
      font-size: 32px;
      line-height: 39px;
      color: #000000;
    }

    .search {
      border: 1px solid #F3F3F3;
      padding: 10px;
      border-radius: 10px;
      display: flex;
      align-items: center;
      position: relative;

      .searchClear {
        position: absolute;
        right: 10px;
      }

    }

    & input {
      border: none;
      font-size: 16px;
      outline: none;
      padding-left: 10px;

    }
  }

  .productItems {
    padding: 40px 0px;
    display: flex;
    gap: 30px;
    flex-wrap: wrap;


  }
}